.advantage {
  width: 100%;
  padding: 25px 18px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: rgb(238, 238, 238);

  @media (min-width: @tablet-width) {
    width: 256px;
    padding: 54px 33px;
  }

  @media (min-width: @desktop-width) {
    width: 400px;
    padding: 63px 50px 41px 50px;
  }
}

.advantage__title {
  margin: 0 auto;
  margin-bottom: 11px;
  text-align: center;
  text-transform: uppercase;
  line-height: 26px;
  font-weight: bold;
  font-size: 18px;

  @media (max-width: @mobile-width-only) {
    br {
      display: none;
    }
  }

  @media (min-width: @tablet-width) {
    font-size: 21px;
  }

  @media (min-width: @desktop-width) {
    width: 150px;
  }
}

.advantage__description {
  margin: 0 auto;
  margin-top: 17px;
  text-align: center;
  text-transform: uppercase;
  line-height: 21px;
  font-size: 14px;

  @media (min-width: @tablet-width) {
    margin-top: 25px;
  }

  @media (min-width: @desktop-width) {
    width: 270px;
  }
}
