.main-header {
  position: relative;
  display: flex;
  flex-direction: column;
  color: #000000;
}
.main-header__logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  width: 185px;
  z-index: 2;
}
.main-header__logo img {
  display: block;
}
.main-nav {
  position: relative;
  width: auto;
  min-height: 56px;
}
.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 55px;
  height: 55px;
  padding: 0;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
.main-nav__toggle:hover,
.main-nav__toggle:active {
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  left: 12px;
  width: 22px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #b3d1e7;
  box-shadow: 0 10px 0 0 #b3d1e7, 0 20px 0 0 #b3d1e7;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: #d9e8f2;
  box-shadow: 0 10px 0 0 #d9e8f2, 0 20px 0 0 #d9e8f2;
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
}
.main-nav--opened {
  z-index: 3;
}
.main-nav--opened .main-nav__toggle {
  top: 0;
  right: 0;
  left: auto;
  background-color: transparent;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 25px;
  left: 10px;
  width: 28px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: #cde1ed;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: #a7cae0;
}
.main-nav--nojs {
  z-index: 3;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__list {
  position: static;
  min-height: 0;
}
.main-nav--nojs .site-list__item {
  display: block;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: absolute;
  width: 100%;
}
@media (min-width: 768px) {
  .site-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    position: static;
  }
}
@media (max-width: 767px) {
  .site-list::before {
    content: "";
    position: absolute;
    bottom: 0;
    z-index: 2;
    width: 100%;
    height: 7px;
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }
}
.site-list__item {
  flex-basis: 20%;
}
@media (min-width: 768px) {
  .site-list__item:nth-child(3),
