}
.site-list .site-list__link {
  position: relative;
  display: block;
  text-align: center;
  padding: 17px 0 12px 0;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
